<template>
  <div>
    <mt-header title="查询预约信息" color="rgb(0,204,153)">
    	<router-link to="/" slot="left">
    		<mt-button icon="back">返回</mt-button>
    	</router-link>
    </mt-header>
    <mt-field class="idcardBox" label="身份证号" placeholder="请输入身份证号码" v-model="idcard"></mt-field>
    <div class="appoint" @click="query">查询</div>
  </div>
</template>

<script>
  import Vue from 'vue'
  const axios = require('axios')
  import { Indicator,Toast } from 'mint-ui'
  export default {
    data(){
      return{
        idcard:''
      }
    },
    methods:{
      query () {
        Indicator.open('加载中...')
        axios({
          method: 'post',
          url: 'https://www.c65z45.cn/api/Reserve/GetOpenReserveList',
          data: {
            IDCardNo: this.idcard,
            Token: 'B68DE9D1F8584D7099BCE47144D0351C'
          }
        }).then(res => {
          Indicator.close()
          if(res.data.IsSuccess){
            Vue.prototype.$userMsg = res.data.Data
            this.$router.push({
            	path: '/resultlist'
            })
          }else{
            Toast({
              message: res.data.Message,
              position: 'middle',
              duration: 3000
            })
          }

        }).catch(error => {
          Indicator.close()
          Toast({
            message: '服务器错误',
            position: 'middle',
            duration: 3000
          })
        })
      }
    }
  }
</script>

<style scoped>
  .appoint{
  	margin: 0.3rem auto 0 auto;
  	width: 95%;
  	background: rgb(0,204,153);
  	color: #fff;
  	text-align: center;
  	line-height: 1rem;
  	border-radius: 0.1rem;
  	font-size: 0.35rem;
  	font-weight: 600;
  }
  .mint-header{
  	background: rgb(0,204,153);
  }
</style>
